.introduction{
  position: absolute;
  top: 0;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}
.introduction .page{
  position: absolute;
  width: 100%;
  height: 100vh;
}
.introduction .page1{
  background: #3cccc4;
}
.introduction .page2{
  background: #f9da78;
}
.introduction .page3{
  background: #f983ab;
}
.introduction .page4{
  background: #bbb5f2;
}
.slide-button-container{
  position: fixed;
  bottom: 20px;
  width: 100%;
  height: 40px;
  /* background: #fff3; */
}
.slide-button{
  width: 50px;
  margin: 0 auto;
  background: transparent;
  border: 0;
  outline:none;
}
.sign{
  position: absolute;
  top: 40vh;
  left: 20px;
  width: 10px;
  height: 10vh;
  /* background: #fff6; */
}
.sign-button{
  margin-bottom: 10px;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  background: #fff8;
}
.slide-enter-active, .slide-leave-active{
  -webkit-transition: all .8s;
  transition: all .8s;
}
.slide-enter{
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}
.slide-leave-to{
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.introduction-enter-active, .introduction-leave-active{
  -webkit-transform-origin: 0% 0%;
          transform-origin: 0% 0%;
  -webkit-transition: 1s;
  transition: 1s;
}
.introduction-enter, .introduction-leave-to{
  -webkit-transform: scaleY(0.35);
          transform: scaleY(0.35);
}













